
import React, {useState, useEffect} from 'react'
import matchesService from '../services/matches'
import MatchData from './MatchData'

const Matches = ({ queryName }) =>{

    const [matches, setMatches] = useState([])
    const [render, setRender] = useState(false)
    const [showNames, setShowNames] = useState(false)

    async function fetchData (){
        if (!(render)) return;
        const matches = await matchesService.getMatches(queryName)
        setMatches(matches)
    }

    useEffect(() => {
        fetchData()
    }, [render])

    const showButtonHandler = () => {
        setRender(true)
        setShowNames(!showNames)
    }

    const handleCheck = () => {
        return
    }

    return(
        <div>
            <button onClick={() => showButtonHandler()}>toggle</button>
            <div>
            {showNames && matches.map(m => <li><MatchData match={m}/><input name="visited" type="checkbox"
            checked={m.visited} onChange={handleCheck}></input></li>)}  
            </div>
        </div>
    )
}

export default Matches